import React from "react";
import { screen } from "@testing-library/react";
import App from "./App";
import { renderWithRouter } from "./testUtils";

test("renders App component", () => {
  // If your App already includes a Router, set useMemoryRouter to false
  renderWithRouter(<App />, { useMemoryRouter: false });

  // Check for elements that you know should be in your App
  // For example, if you have a header:
  const headerElement = screen.getByRole("heading");
  expect(headerElement).toBeInTheDocument();

  // Or, if you have a main content area:
  const mainElement = screen.getByRole("main");
  expect(mainElement).toBeInTheDocument();
});
